body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 80%;
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
.wrapper {
    background-color: rgb(255, 255, 255);
    height: 100%;
    width: 100%;
    justify-content: space-around;
    align-content: flex-start;
    display: flex;
    flex-flow: row wrap;

}

